(function (Drupal, once) {

  Drupal.behaviors.neoTomSelect = {
    attach: () => {
      once('neo.tom', '.neo-multi-select').forEach(el => {
        const parent = el.parentElement;
        if (parent) {
          parent.classList.add('neo-multi-select-wrapper');
        }
        if (el instanceof HTMLSelectElement) {
          console.log('EL', el, el.value);
          el.value = el.value || '';
        }

        var settings = {
          plugins: {
            remove_button: {
              title:'Remove this item',
            }
          },
          onItemAdd:function() {
            console.log('THIS', this);
              // this.setTextboxValue('');
              // this.refreshOptions();
          }

        };
        const control = new TomSelect(el, settings);
        control.removeOption('_none');
      });
    }
  };

})(Drupal, once);

export {};
